import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Film } from 'src/app/services/interfaces/film';
import { DataFilmService } from 'src/app/services/data-film.service';
import { NavController } from '@ionic/angular';

@Component({
	selector: 'app-info-film',
	templateUrl: './info-film.page.html',
	styleUrls: [ './info-film.page.scss' ]
})
export class InfoFilmPage implements OnInit {
	selectedFilm: Film;

	constructor(
		private navCtrl: NavController,
		private serviceFilm: DataFilmService,
		private activatedRoute: ActivatedRoute
	) {}

	ngOnInit() {
		this.activatedRoute.paramMap.subscribe((paramMap) => {
			if (!paramMap.has('idFilm')) {
				this.navCtrl.navigateBack([ '/medias/films' ]);
				return;
			}
			this.selectedFilm = this.serviceFilm.getInMyFilm(paramMap.get('idFilm'));
		});
	}

	goBack() {
		this.navCtrl.navigateBack([ '/medias/films' ]);
	}
}
